import { Component } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCurrentUserId, selectCurrentUserPictureId, selectIsLoadingAuth } from '@store/auth/auth.selectors';
import { Observable } from 'rxjs';
import { URLS } from '@enums/urls';

@Component({
  selector: 'app-delete-account',
  templateUrl: './delete-account.component.html',
  styleUrl: './delete-account.component.scss',
})
export class DeleteAccountComponent {
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingAuth);
  userId$: Observable<string | null> = this.store.select(selectCurrentUserId);
  pictureId$: Observable<string | null> = this.store.select(selectCurrentUserPictureId);
  isCheckboxChecked: boolean = false;
  showDeletePopup: boolean = false;

  constructor(private store: Store<AppStateInterface>) {}

  showDeleteConfirmationDialog() {
    if (this.isCheckboxChecked) {
      this.showDeletePopup = true;
    }
  }

  hideDeleteConfirmationDialog() {
    this.showDeletePopup = false;
  }

  confirmDeleteAccount() {
    
    // Close the pop-up after confirmation
    this.hideDeleteConfirmationDialog();
  }

  protected readonly URLS = URLS;
}
